@import "../../stylesheet/base";
@import "../../stylesheet/fonts";
@import "../../stylesheet/vars";
@import "../../stylesheet//mixins";

.overlay {
  // align-items: start;
  margin-top: 60px;
  background-color: $white-color;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  z-index: 1200;
  @include mq(mobile){
    align-items: center;
  }
  @include mq(mobile){
    margin-top: 0px;
    background-color: rgba(0, 0, 0, 0.8);
  }
}

.modal {
  border-radius: 20px;
  width: 540px;
  max-width: calc(100vw - 48px);
  max-height: calc(100vh - 24px);
  background-color: #fff;
}
.btn {
  outline: none;
}